<template>
  <div class="maxset">
    <div class="overflowtable mb-3">
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">商品</th>
            <th scope="col">比例</th>
            <th scope="col">數量</th>
            <th scope="col">價格</th>
            <th scope="col">刪除</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in shopjson" :key="index">
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ item.name }}({{item.size}})</td>
            <td>{{ item.sweet + item.ice }}</td>
            <td>{{ item.count }}</td>
            <td>{{ item.price }}</td>
            <td>
              <font-awesome-icon @click="del(index)" :icon="['fas', 'trash']" />
            </td>
          </tr>
          <!-- <tr v-if="shopjsonlength >= 1">
            <td class="text-white text-right pr-3 fontsize" colspan="6">
              <span class="mr-1">杯數 {{ totalcount }} / </span>
              <span>總價格 {{ totalprice }}</span>
            </td>
          </tr> -->
          <tr v-if="shopjsonlength == 0">
            <td class="text-white text-center" colspan="6">
              <span>購物車為空，趕緊購入!!!</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="text-white text-right pr-5 mb-5 fontsize">
      <span class="mr-1">商品數量 {{ totalcount }} / </span>
      <span>總價格 {{ totalprice }}</span>
    </div>
    <From ref="user" v-if="shopjsonlength > 0"></From>
  </div>
</template>

<script>
import From from "./from.vue";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
library.add(faTrash);
export default {
  name: "Shop",
  data() {
    return {
    };
  },
  computed: {
    shopjson() {
      return this.$store.state.shopcar;
    },
    shopjsonlength() {
      return this.shopjson.length;
    },
    totalprice() {
      const every = this.shopjson.map((item) => item["price"]);
      const idList = every.reduce((pre, n) => {
        return pre + n;
      }, 0);
      return idList;
    },
    totalcount() {
      const every = this.shopjson.map((item) => item["count"]);
      const idList = every.reduce((pre, n) => {
        return pre + n;
      }, 0);
      return idList;
    },
  },
  methods: {
    del(index) {
      this.$store.commit("del", index);
    },
  },
  components: {
    From,
  },
};
</script>

<style lang="scss">
.maxset {
  max-width: 800px;
  margin: auto;
  .overflowtable {
    max-height: 400px;
    overflow-y: auto;
    .table {
      th {
        font-size: 24px;
      }
      td {
        font-size: 18px;
      }
    }
    .fontsize {
      font-size: 20px;
    }
  }
}

@media (max-width: "576px") {
  .table {
    th {
      font-size: 3.2vw !important;
      padding: 5px;
    }
    td {
      font-size: 2.6vw !important;
      padding: 5px;
    }
  }
}
</style>


