<template>
  <div class="buttons d-flex justify-content-center mb-4">
    <router-link
      to="/menu/selling"
      tag="button"
      active-class="active"
      class="btn btn-secondary btn-lg m-2"
      tabindex="-1"
      role="button"
      aria-disabled="true"
      >熱銷商品</router-link
    >
    <router-link
      to="/menu/limitedtime"
      tag="button"
      class="btn btn-secondary btn-lg m-2"
      tabindex="-1"
      role="button"
      aria-disabled="true"
      >期間限定</router-link
    >
  </div>
</template>

<script>
export default {
  name: "Buttons",
  data() {
    return {};
  },
};
</script>

<style lang="scss">
@media (max-width: "576px") {
  .buttons {
    margin: auto;
    .btn{
      font-size: 14px;
    }
  }
}
</style>


