// 開始配置路由相關

//引用工具
import VueRouter from 'vue-router';
import Vue from 'vue'

// 導入跳路由的組件
// import home from '../components/home.vue';
// import about from '../components/about.vue';
// import more from '../components/more.vue';
// import user from '../components/user.vue';

// 懶加載入方式
const selling = () => import('../components/selling.vue');
const limitedtime = () => import('../components/limitedtime.vue');
const shop = () => import('../components/shop.vue');
const sweet = () => import('../components/sweet.vue');
const menu = () => import('../components/menu.vue');


// 1. 通過vue.use(插件) 安裝插件
Vue.use(VueRouter);

// 增強寫法
// 1個映射關係1個對象
const routes = [
  {
    // 預設
    path: '/',
    // 當為預設時，重新定義導向
    redirect: '/sweet'
  },
  {
    path: '/sweet',
    component: sweet
  },
  {
    path: '/shop',
    component: shop
  },
  {
    path: '/menu',
    component: menu,
    children: [
      {
        path: '/',
        redirect: 'selling'
      },
      {
        path: 'selling',
        component: selling,
        meta: {
          title: '熱銷商品',
        },
      },
      {
        path: 'limitedtime',
        component: limitedtime,
        meta: {
          title: '期間限定',
        },
      },
    ]
  },
  // {
  //   path: '/about',
  //   component: about,
  //   meta: {
  //     title: '關於'
  //   },
  //   //設定進入之前的動作
  //   beforeEnter: (to, from, next) => {
  //     console.log();
  //     next();
  //   }
  // }
]


// 2. 開始創建路由對象
const router = new VueRouter({
  //配置路由和組件的應用關係
  routes,

  //模式 -> 讓url不出現#
  mode: 'history',

  // 誰處於活耀狀態的話 class名稱就改為該設定的名稱
  // 此權限大於在標籤內的設定
  linkActiveClass: 'active',

  // base:'/'
})

// 導航守衛
// => 監聽從A跳到B做的事情
// router.beforeEach((to, from, next) => {
//   // 基本使用
//   // document.title = to.meta.title;

//   //如果路由有多層的話
//   document.title = to.matched[0].meta.title;

//   //log 測試
//   console.log('-----');

//   next();
// })

// => 監聽從A跳到B做的事情
// router.beforeEach((to, from, next) => {
//   // 基本使用
//   // document.title = to.meta.title;

//   //如果路由有多層的話
//   // document.title = to.matched[0].meta.title;

//   //log 測試
//   console.log('+++++');

//   next();
// })

// 還有組件各自的路由守衛

//---------------------------------------------

// 3.將router掛載到vue實力上面
//   將其導出，讓main.js導入
export default router





