import Vue from "vue";
import axios from "axios";
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

export default {
    getMenuJson(context) { //抓假資料
        axios
            .get("/static/demo.json")
            .then((response) => {
                console.log(response.data);
                context.commit('inState', response.data)
            });
    }
}


