<template>
  <nav class="navbar navbar-light">
    <div class="container pl-4 pr-4">
      <div>
        <router-link to="/sweet" class="navbar-brand">美味甜品</router-link>
        <router-link to="/menu" class="navbar-brand">飲品清單</router-link>
      </div>
      <div>
        <router-link class="navbar-brand" to="/shop">
          <span>購物車</span>
          <font-awesome-icon class="shop" :icon="['fas', 'shopping-cart']" />
        </router-link>
        <span class="mr-2">{{ getshopcarlength }}</span>
      </div>
    </div>
  </nav>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faShoppingCart, faList } from "@fortawesome/free-solid-svg-icons";
library.add(faShoppingCart, faList);
export default {
  name: "hellovuex",
  data() {
    return {};
  },
  computed: {
    getshopcarlength() {
      return this.$store.getters.getshopcarlength;
    },
  },
  methods: {
    shopdom() {
      console.log(1);
      const shopicon = document.querySelector(".shop");
      shopicon.classList.add("ani");
      setTimeout(() => {
        shopicon.classList.remove("ani");
      }, 550);
    },
    finishdom() {
      console.log(2);
      const shoplist = document.querySelector(".shoplist");
      shoplist.classList.add("ani");
      setTimeout(() => {
        shoplist.classList.remove("ani");
      }, 550);
    },
  },
  watch: {
    getshopcarlength() {
      this.shopdom();
    },
  },
};
</script>

<style lang="scss" >
nav {
  background: rgb(81, 60, 32) !important;
    
  .container {
    .navbar-brand {
      color: #fff;
      font-size: 32px;
    }
    .shop.ani {
      animation: ani 0.5s;
    }
    span {
      font-size: 20px;
      color: #fff;
    }
  }
}
@media (max-width: "992px") {
  nav {
    .container {
      .navbar-brand {
        font-size: 3.5vw;
        margin-right: 6px;
      }
      .shop.ani {
        animation: ani 0.5s;
      }
      span {
        font-size: 2vw;
      }
    }
  }
}

@media (max-width: "576px") {
  nav {
    .container {
      padding: 0 8px !important;
      .navbar-brand {
        font-size: 3.8vw;
      }
      .shop.ani {
        animation: ani 0.5s;
      }
      span {
        font-size: 2.6vw;
      }
    }
  }
}

@keyframes ani {
  0% {
    color: rgb(0, 0, 0);
  }
  50% {
    color: red;
  }
  100% {
    color: rgb(0, 0, 0);
  }
}
</style>


