<template>
  <div class="mb-3">
    <div class="form-group row">
      <label class="col-4 col-form-label text-left" for="text1">姓名：</label>
      <div class="col-8">
        <input
          id="text1"
          name="text1"
          type="text"
          class="form-control"
          v-model="username"
        />
      </div>
    </div>
    <div class="form-group row">
      <label for="text" class="col-4 col-form-label text-left">電話：</label>
      <div class="col-8">
        <input
          id="text"
          name="text"
          type="text"
          class="form-control"
          v-model="userphone"
        />
      </div>
    </div>
    <div class="form-group row" v-show="get == '外送'">
      <label for="text2" class="col-4 col-form-label text-left">地址：</label>
      <div class="col-8">
        <input
          id="text2"
          name="text2"
          type="text"
          class="form-control"
          v-model="useraddress"
        />
      </div>
    </div>
    <div class="form-group row mb-4">
      <label class="col-4 text-left">取物：</label>
      <div class="col-8">
        <div class="custom-control custom-radio custom-control-inline">
          <input
            name="radio"
            id="radio_0"
            type="radio"
            class="custom-control-input"
            value="自取"
            v-model="get"
            checked="get"
          />
          <label for="radio_0" class="custom-control-label">來店自取</label>
        </div>
        <div class="custom-control custom-radio custom-control-inline">
          <input
            name="radio"
            id="radio_1"
            type="radio"
            class="custom-control-input"
            value="外送"
            v-model="get"
          />
          <label for="radio_1" class="custom-control-label">外送</label>
        </div>
      </div>
    </div>
    <div class="form-group row">
      <label class="col-4"></label>
      <div class="col-8 text-center">
        <div class="custom-control custom-checkbox custom-control-inline">
          <input
            name="checkbox"
            id="checkbox_0"
            type="checkbox"
            class="custom-control-input"
            value="rabbit"
            v-model="finalcheck"
          />
          <!-- checked="checked" -->
          <label for="checkbox_0" class="custom-control-label"
            >本人已確定訂購人資訊及商品詳細。</label
          >
        </div>
      </div>
    </div>
    <div class="text-right pr-5 frombtn">
      <button class="btn btn-danger" :disabled="btnctl" @click="checkout">結帳</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "From",
  data() {
    return {
      finalcheck: false,
      username: "",
      userphone: "",
      useraddress: "",
      get: "自取",
    };
  },
  methods:{
    checkout(){
      alert('訂購成功，即將返回');
      this.$router.push('/')
    }
  },
  computed: {
    btnctl() {
      if (this.get == '請填寫地址') {
          return
      }
      if (this.get == '自取') {
          this.useraddress = '請填寫地址'
      }
      return (this.finalcheck != false &&
        this.username != "" &&
        this.userphone != "" &&
        this.useraddress != "" &&
        this.get != ""
      )? false
        : true;
    },
  },
};
</script>

<style lang="scss">
.form-group {
  color: white;
  .col-4 {
    font-size: 18px;
    max-width: 100px;
  }
}

@media (max-width: "576px") {
  .form-group {
    color: white;
    .col-4 {
      font-size: 4vw;
      width: 2vw;
    }
  }
  .frombtn{
    display: block;
    padding-right: 2vw !important;
  }
}
</style>


