<template>
  <div class="containergrid container">
    <div class="row ps-5">
      <div class="col p-3">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import Buttons from "./buttons.vue";
import Shop from "./shop.vue";

export default {
  name: "Containergrid",
  data() {
    return {};
  },
  components: {
    Buttons,
    Shop
  },
};
</script>

<style lang="scss">
.containergrid {
  min-height: calc(100vh - 74px);
  .table th, .table td{
    border-top: unset;
    color: #fff;
  }
}
</style>


